.sideNav-container{
    position: fixed;
    top: 20%;
    width: 3pc;
    z-index: 100020;
    background: none;
    right: 0;
    float: right;
    left: auto;
}

.sideNav-container .link{
    position: relative;
    display: block;
    width: 3pc;
    margin: 0;
    text-align: center;
    overflow: hidden;
    background: #0e143c;
    z-index: 100030;
    cursor: pointer;
    padding: 12px;
    filter: drop-shadow(0.35rem 0.35rem 0.4rem rgba(0, 0, 0, 0.5));
}

.sideNav-container .iconBar{
    display: block;
    width: 22px;
    height: 2px;
    margin-bottom: 4px;
    margin-top: 4px;
    border-radius: 1px;
    background-color: #bd6964;
}